<template>
  <div>
    <mt-header title="理财分享业务平台">
    </mt-header>
    <span class="header-btn right" style="background: url('./static/img/header/setting.png')"></span>
    <div class="inform-overview" style="background: url('./static/img/business/bg.png')">
      <div class="avatar-border" style="background: url(./static/img/business/avatar_border.png)">
        <div class="avatar" :style="'background: url('+ datas.avatar +')'"></div>
      </div>
      <div class="name">
        {{datas.name}}</div>
      <div class="job">
        <div class="company">{{datas.company}}</div>
        <div class="jobtitle">{{datas.jobtitle}}</div>
      </div>
      <div class="applyfor">
        <p><span class="num">51</span>项</p>
        <p>申请</p>
      </div>
      <div class="gap"></div>
      <div class="consult">
        <p><span class="num">5</span>人</p>
        <p>咨询</p>
      </div>
    </div>
    <div class="achievement" style="background: url(./static/img/business/achievement_btn.png)">
      <div class="icon" style="background: url(./static/img/business/achievement_cup.png)"></div>
      <span>业绩</span>
    </div>
  </div>
</template>
<script>
export default {
  data: function() {
    return {

    }
  },
  props: {
    datas: Object
  },
  components: {},
  mounted: function() {
    console.log(this.datas.avatar)
  },
  methods: {}
}
</script>
<style scoped>
.inform-overview {
  position: relative;
  width: 100%;
  height: 4.08rem;
  background-repeat: no-repeat!important;
  background-size: 100% 100%!important;
  color: #fff;
}

.avatar-border {
  position: relative;
  top: 0.12rem;
  left: 50%;
  width: 1.34rem;
  height: 1.34rem;
  margin-left: -0.67rem;
  background-repeat: no-repeat!important;
  background-size: 100% 100%!important;
}

.avatar {
  position: relative;
  top: 0.04rem;
  left: 50%;
  width: 1.26rem;
  height: 1.26rem;
  margin-left: -0.63rem;
  background-repeat: no-repeat!important;
  background-size: contain!important;
}

.name {
  position: relative;
  top: 0.14rem;
  width: 100%;
  text-align: center;
  font-size: 0.3rem;
}

.job {
  position: relative;
  top: 0.2rem;
  width: 100%;
  text-align: center;
  color: #AFDECA;
  font-size: 0.24rem;
}

.job>* {
  display: inline-block;
  padding: 0.05rem 0.08rem;
  background-color: #00aa58;
}

.job>.company {
  padding-right: 0;
  border-top-left-radius: 0.2rem;
  border-bottom-left-radius: 0.2rem;
}

.job>.jobtitle {
  margin-left: -0.12rem;
  padding-left: 0;
  border-top-right-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
}

.job>.jobtitle:before {
  content: '|';
}

.applyfor,
.consult {
  position: absolute;
  top: 2.3rem;
  font-size: 0.22rem;
  text-align: center;
}

.applyfor .num,
.consult .num {
  font-size: 0.7rem;
}

.applyfor p:nth-child(2),
.consult p:nth-child(2) {
  font-size: 0.26rem;
}

.applyfor {
  left: 1.16rem;
}

.consult {
  right: 1.16rem;
}

.gap {
  position: absolute;
  left: 50%;
  margin-left: -0.005rem;
  top: 2.5rem;
  width: 0.01rem;
  height: 0.8rem;
  background-color: rgba(255, 255, 255, 0.6);
}

.achievement {
  position: absolute;
  top: 5.02rem;
  left: 50%;
  margin-left: -1.3rem;
  width: 2.5rem;
  height: 0.7rem;
  background-repeat: no-repeat!important;
  background-size: contain!important;
  font-size: 0.34rem;
  color: #f06b2f;
  text-align: center;
  line-height: 0.68rem;
}

.achievement span {
  padding-left: 0.4rem;
}

.achievement .icon {
  position: absolute;
  top: 0.18rem;
  left: 0.6rem;
  width: 0.33rem;
  height: 0.33rem;
  background-repeat: no-repeat!important;
  background-size: contain!important;
}
</style>



